import { MediaMatcher } from '@angular/cdk/layout';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';

@Component({
  selector: 'scheibentoenung-schmieder-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss'],
})
export class NavbarComponent {
  isMobile = this.media.matchMedia('(max-width: 600px)');
  private _mobileQueryListener: () => void;



  constructor(changeDetectorRef: ChangeDetectorRef,private  media: MediaMatcher) {
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.isMobile.addListener(this._mobileQueryListener);
  }
}
