import { Component } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { ScullyRoutesService } from '@scullyio/ng-lib';
import { iif, of } from 'rxjs';
import { filter, map, switchMap, tap } from 'rxjs/operators';
import { RouterHelperService } from './services/router-helper.service';

@Component({
  selector: 'scheibentoenung-schmieder-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  data$ = this.routerHelperService
    .getDeepSnapshot()
    .pipe(map(({ data }) => data));
  coverFromRouteData$ = this.data$.pipe(map(({ cover }) => cover));

  coverFromScully$ = this.scully.getCurrent().pipe(map(({ cover }) => cover));

  cover$ = this.coverFromRouteData$.pipe(
    switchMap((cover) => iif(() => !cover, this.coverFromScully$, of(cover)))
  );

  constructor(
    private router: Router,
    private scully: ScullyRoutesService,
    public routerHelperService: RouterHelperService
  ) {}
  title = 'scheibentoenung';
}
